import ThinLine from '../others/ThinLine';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import {products} from '../DUMMY_DATA'
const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 8
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 5
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 3
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1
    }
  };
const MultiSlider = (props)=>{
    return(
        <div className="multi-slider-con">
            <h3>{props.heading}</h3>
            <ThinLine/>
            <Carousel responsive={responsive}>
                {products.map((product)=>(
                    
                <div className="multi-carousal-item">
                    <img src={product.imgUrl} alt={product.name} />
                    <p className="name">{product.name}</p>
                    <p className="price">{product.price}
                    <span className="cutted-price">{product.cuttedPrice}</span>
                    </p>
                </div>  
                ))}
            </Carousel>
        </div>
        );
}
export default MultiSlider;