
import React, {useState} from 'react';
import SearchBar from './SearchBar'
import ThinLine from '../others/ThinLine'

import './header.css'
import { display } from '@mui/system';

const Header = ()=>{
    const [openMenu,setMenuOpen]= useState(false);
    function openPhnMenu(){
        setMenuOpen(true)

    }
    function closePhnMenu(){
        setMenuOpen(false)
    }
     return(
        <div className="header-con">
            <header>
                <div className="left">Dark Cart</div>
                <div className="right">
                    {/* Big screen menu  */}
                    <menu className="main-menu">
                        <SearchBar  />
                        <li>Products</li>
                        <li>
                            <div className="badge-con">
                             <i className="fas fa-shopping-cart" />
                             <div className="badge">12</div>
                            </div>
                        </li>
                    </menu>
                    {/* Phn Menu  */}
                    <i className="fa fa-bars phn-menu-btn" onClick={openPhnMenu}></i>
                    <div style={{display:openMenu ?"block"  :"none"}} className="phn-menu-con">
                        <div className="phn-menu">
                            <li style={{marginBottom:"36px",textAlign:"right"}} className="close-phn-menu">
                                <i onClick={closePhnMenu}  className="fa fa-close " />
                            </li>
                            <li><SearchBar noMargin={true}/></li>
                            <ThinLine/>
                            <li className="phn-menu-item">Product</li>
                            <ThinLine/>
                            <div className="phn-badge-con"><li className="phn-menu-item">Cart</li>
                            <div className="phn-badge">12</div></div>
                            <ThinLine/>
                        </div>
                    </div>
                </div>
            </header>
        </div>
    )
}
export default Header;